import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.scss']
})
export class TimerComponent implements OnInit {

  public days: number;
  public hours:number;
  public minutes:number;
  public seconds:number;
  public cwidth:number;
  public swidth:number;

  constructor() { }

  ngOnInit() {
    const year = new Date().getFullYear();
    const bday = new Date(year, 6, 27).getTime();

    // countdown
    setInterval(() => {

      // get today's date
      const today = new Date().getTime();

      // get the difference
      const diff = bday - today;

      // math
      this.days = Math.floor(diff / (1000 * 60 * 60 * 24));
      this.hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      this.minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      this.seconds = Math.floor((diff % (1000 * 60)) / 1000);

    }, 1000);

    let width = screen.width;
    if(width < 650){
      width = Math.abs(width - 650);
      width = width/650;
      document.body.style.zoom = (width * 100) + '%';
      let ele = document.getElementsByTagName("app-timer")[0] as HTMLElement;
      // ele.style.left = "42%";
    }
  }

}
